import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { SignupService } from 'src/app/services/signup/signup.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { ErrorService } from 'src/app/services/errors/error.service';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {

  constructor(
    private fb: FormBuilder, private signupService: SignupService,
    private spinner: NgxSpinnerService, private errorService: ErrorService
  ) { }

  successMsg: string;
  errorMsg: string;

  forgotPasswordForm = this.fb.group({
    email: ['', [Validators.required, Validators.email]],
  });

  ngOnInit(): void {
  }

  forgotPassword() {
    console.log('working forgot', this.forgotPasswordForm.value);

    this.spinner.show();

    this.signupService.forgotPassword(this.forgotPasswordForm.value).subscribe((res: any) => {
      console.log('forgot res', res);
      this.spinner.hide();
      if (res.status === true) {
        this.forgotPasswordForm.reset();
        this.successMsg = res.message || 'Check Email For Login Details.';
      }

    }, (httpError)=> {
      this.spinner.hide();
      this.errorMsg = this.errorService.errorMsg;
    });
  }
}
